.tooltip {
  position: relative;
}

.tooltip-text {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  left: 0;
  padding: 5px;
  position: absolute;
  top: 0;
}
.Community .Community_content .slider-img-wrapper img {
  transition: 0.8s;
  width: 0%;
  height: 500px;
  object-fit: cover;
}
.Community .Community_content .slider-img-wrapper img.fadein {
  width: 100%;
}
.Community .container-fluid {
  padding: 0px 100px;
}
.Community .Community_content {
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.po {
  /* position: sticky; */
  top: 0px;
  z-index: 99;
  padding-bottom: 250px;
  transition: all 0.4s ease-in-out;
  padding: 150px 0px;
}
.Community .mt-comm {
