/* full header css starts here */
.header_full {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	font-size: 17px;
	padding: 1.5rem 0;
	font-family: "Montserrat-Light";
	/* font-family: ; */
}

.header {
	position: fixed;
	color: white;
	z-index: 5;
	width: 100%;
	transition: all 0.4s ease-in-out;
}
.header .container-fluid {
	padding: 0px 100px;
}
.header .logo-wrapper {
	flex: 0 0 20%;
}

.header_full .header_links {
	font-size: 17px;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-self: center;
	width: 100%;
	color: white;
	flex: 0 0 60%;
}

.header_full .header_links_black {
	font-size: 17px;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-self: center;
	width: 100%;
	color: black;
}

.header_full .header_links span {
	margin: 0 1rem;
}

.header_full .hdr_content_spilt {
	display: flex;
	flex-direction: column;
}
.header_full .hdr_content_spilt span {
	cursor: pointer;
	font-size: 16px;
	line-height: 1.3;
}
/* full header css ends here */
.-top-24 {
	top: -6rem;
}
.home_page .top-0 {
	background: #000;
}
